/* https://css-tricks.com/snippets/css/star-wars-crawl-text/ */

.intro {
  display: grid;
  place-items: center;
  overflow: hidden;
  color: yellow;
  background: #000;
  height: 100vh;
  width: 100vw;
}

.content {
  display: flex;
  justify-content: center;
  height: 100vh;
  -webkit-perspective: 75rem;
  perspective: 75rem;
  color: #feda4a;
  font-family: 'Pathway Gothic One', sans-serif;
  font-weight: 600;
  letter-spacing: 6px;
  text-align: center;
}

.crawl {
  position: relative;
  -webkit-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
  top: -100px;
  -webkit-animation: crawl 60s linear;
  animation: crawl 60s linear;
}

.fade {
  position: relative;
  width: 100%;
  min-height: 60vh;
  top: -25px;
  background-image: linear-gradient(0deg, transparent, black 75%);
  z-index: 1;
}

.title {
  font-size: 600%;
}

.sub-title {
  font-size: 200%;
}

.text-content {
  font-size: 250%;
  line-height: 150%;
  margin-top: 8rem;
  text-align: justify;
}

@-webkit-keyframes crawl {
  0% {
    top: 0;
    -webkit-transform: rotateX(20deg) translateZ(0);
    transform: rotateX(20deg) translateZ(0);
  }

  100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-3500px);
    transform: rotateX(25deg) translateZ(-3500px);
  }
}

@keyframes crawl {
  0% {
    top: 0;
    -webkit-transform: rotateX(20deg) translateZ(0);
    transform: rotateX(20deg) translateZ(0);
  }

  100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-3500px);
    transform: rotateX(25deg) translateZ(-3500px);
  }
}

@media screen and (min-width: 680px) {
  .title {
    font-size: 700%;
  }

  .sub-title {
    font-size: 300%;
  }

  .text-content {
    font-size: 400%;
    line-height: 150%;
    margin-top: 8rem;
  }
}

